import { Link } from 'react-router-dom';
import OtpInput from "react-otp-input";


import { useState } from 'react';


//CSS
import '../assets/css/pages/login.css';

// Images
import IcGoogle from "../assets/images/icons/ic-google.svg"
import IcFbLg from "../assets/images/icons/ic-fb-lg.svg"
import IcApple from "../assets/images/icons/ic-apple.svg"

function Otp() {

    const [otp, setOtp] = useState("");

    const handleOtpInput = (otp) => {
        setOtp(otp);
    };

    return (
        <section className="auth-form padding-60">
            <div className="container">
                <form>
                    <div className="form-box">
                        <div className="section-title varient-two">
                            <h4>otp verification</h4>
                            <p>We've sent a verification code to your email-johndoe123@gmail.com</p>
                        </div>
                        <div className="otp-form form-group">
                            <label className="form-label">Enter OTP</label>
                            <OtpInput
                                value={otp}
                                onChange={handleOtpInput}
                                numInputs={4}
                                containerStyle="otpContainer"
                                inputStyle="otp-input form-control"
                            />
                        </div>
                        <div className="row form-submit-btn">
                            <div className="col-lg-7">
                                <button className="button button-blue w-100" type="submit">Verify</button>
                            </div>
                        </div>
                        <div className='row'>
                            <div className='col-lg-7'>
                                <div className='otp-metas d-flex justify-content-between'>
                                    <div>
                                        Didn't Get it?
                                        <button className='text-button'>Resend OTP</button>
                                    </div>
                                    <span className='otp-timer'>00.28</span>
                                </div>
                            </div>
                        </div>

                    </div>
                </form>
            </div>
        </section>
    )
}

export default Otp;